___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --red: #C40C0C;
    --orange: #FF6500;
    --brown: #CC561E;
    --beige: #F6CE71;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
}

body {
    width: 100vw;
}

#content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

ul {
    display: flex;
    list-style-type: none;
    justify-content: center;
    align-items: center;
    gap: 40px;
    background-color: var(--beige);
}

button {
    padding: 10px 50px;
    font-size: 16px;
    margin: 20px 0px;
    border: 0;
    border-radius: 10px;
    background-color: var(--red);
    color: var(--beige);
}

button:hover {
    outline: 1px solid var(--brown);
}

h1 {
    padding-top: 20px;
    padding-bottom: 30px;
    background-color: var(--beige);
    color: var(--red);
    align-self: stretch;
}

h2 {
    margin: 20px 0;
    color: var(--red);
}

p {
    width: 80ch;
    margin: 20px 0;
    font-size: 18px;
    text-align: justify;
    text-align-last: center;
}

span {
    color: var(--brown);
    font-weight: 600;
}`, "",{"version":3,"sources":["webpack://./src/index.css"],"names":[],"mappings":"AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;IACV,iCAAiC;AACrC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;IACT,8BAA8B;AAClC;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,SAAS;IACT,mBAAmB;IACnB,4BAA4B;IAC5B,mBAAmB;AACvB;;AAEA;IACI,+BAA+B;AACnC;;AAEA;IACI,iBAAiB;IACjB,oBAAoB;IACpB,8BAA8B;IAC9B,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,iBAAiB;AACrB;;AAEA;IACI,WAAW;IACX,cAAc;IACd,eAAe;IACf,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,mBAAmB;IACnB,gBAAgB;AACpB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');\r\n\r\n:root {\r\n    --red: #C40C0C;\r\n    --orange: #FF6500;\r\n    --brown: #CC561E;\r\n    --beige: #F6CE71;\r\n}\r\n\r\n* {\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n    padding: 0;\r\n    font-family: \"Roboto\", sans-serif;\r\n}\r\n\r\nbody {\r\n    width: 100vw;\r\n}\r\n\r\n#content {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    text-align: center;\r\n}\r\n\r\nul {\r\n    display: flex;\r\n    list-style-type: none;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 40px;\r\n    background-color: var(--beige);\r\n}\r\n\r\nbutton {\r\n    padding: 10px 50px;\r\n    font-size: 16px;\r\n    margin: 20px 0px;\r\n    border: 0;\r\n    border-radius: 10px;\r\n    background-color: var(--red);\r\n    color: var(--beige);\r\n}\r\n\r\nbutton:hover {\r\n    outline: 1px solid var(--brown);\r\n}\r\n\r\nh1 {\r\n    padding-top: 20px;\r\n    padding-bottom: 30px;\r\n    background-color: var(--beige);\r\n    color: var(--red);\r\n    align-self: stretch;\r\n}\r\n\r\nh2 {\r\n    margin: 20px 0;\r\n    color: var(--red);\r\n}\r\n\r\np {\r\n    width: 80ch;\r\n    margin: 20px 0;\r\n    fo